a svg,
button svg {
  pointer-events: none; }

img {
  max-width: 100%;
  height: auto; }

@media screen and (max-width: 992px) {
  .main-nav {
    display: none; } }

@media screen and (max-width: 992px) {
  .main-nav--open {
    display: block; } }

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none; }

.main-nav__item {
  height: 3.22vmax;
  background: linear-gradient(to bottom, #333b3f 0%, #2e363a 100%);
  border-bottom: 1px solid #293034; }
  @media screen and (max-width: 992px) {
    .main-nav__item {
      height: 6vmax; } }
  .main-nav__item:hover, .main-nav__item:focus {
    background: linear-gradient(to bottom, #18a3a1 0%, #31bcba 100%);
    border-bottom: 1px solid #169291; }

.main-nav__item a {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  width: 100%;
  padding-right: 1.3vmax;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 0.93vmax;
  line-height: 1.66vmax; }
  @media screen and (max-width: 992px) {
    .main-nav__item a {
      justify-content: center;
      font-size: 2.5vmax; } }

.main-nav__item--active {
  background: linear-gradient(to bottom, #18a3a1 0%, #31bcba 100%); }

.main-nav__btn {
  display: none; }
  @media screen and (max-width: 992px) {
    .main-nav__btn {
      display: block;
      font-size: 3vmax;
      color: #fff;
      padding: 3vw;
      width: 100%;
      border: none;
      outline: none;
      text-transform: uppercase;
      background-color: #293034; } }

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 15.62vw;
