  color: #6b7872;
  box-shadow: 8px 0px 14px 0px rgba(70, 70, 70, 0.55); }
  @media screen and (max-width: 992px) {
    .header {
      position: relative;
      width: 100%;
      height: auto; } }

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 11vh;
  background-color: #fff; }
  @media screen and (max-width: 992px) {
