.work-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; }
  .work-list article {
    height: 16.19vw;
    width: 25%;
    position: relative;
    overflow: hidden; }
    @media screen and (max-width: 992px) {
      .work-list article {
