        margin-bottom: 1.25vmax; } }

.work-card__item {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  perspective: 1000px; }

.work-card__item-img {
  width: calc( 100% + 5vw); }

.work-card__item-link {
  position: absolute;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  z-index: 2;
  width: 100%;
  height: inherit;
  background-color: rgba(45, 184, 185, 0.8);
  color: #fff;
  text-decoration: none; }

.work-card__item-link h2 {
  font-size: 1.25vw;
  font-weight: 700;
  margin: 0;
  margin-bottom: 0.78vw; }

.work-card__item-link h3 {
  font-size: 1vw;
  font-weight: 700;
  line-height: 1.3vw;
  margin: 0;
  margin-bottom: 0.5vw; }

.work-card__item-link h3::after {
  content: '';
  display: block;
  width: 70%;
  height: 1px;
  margin: 1vw auto;
  background-color: #fff; }

.work-card__item-icon {
  width: 3vw;
  height: 3vw; }

.work-card__item-front, .work-card__item-back {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  transition: 1s;
  backface-visibility: hidden; }

.work-card__item-front {
  background-color: #fff; }

.work-card__item-back {
  background-size: cover;
  background-position: center;
  z-index: 1;
  transform: rotateY(180deg); }

.work-card__item:hover .work-card__item-front,
.work-card__item:focus .work-card__item-front {
  transform: rotateY(180deg); }

.work-card__item:hover .work-card__item-back,
.work-card__item:focus .work-card__item-back {
  transform: rotateY(360deg); }

.work-card__item-back--mishka {
  background-image: url("../img/project__mishka.jpg"); }

.work-card__item-back--sedona {
  background-image: url("../img/project__sedona.jpg"); }

.work-card__item-back--keksobooking {
  background-image: url("../img/project__keksobooking.jpg"); }

.work-card__item-back--guessmelody {
  background-image: url("../img/project__guessmelody.png"); }

.work-card__item-back--portfolio {
  background-image: url("../img/project__portfolio.jpg"); }

.filter-block {
  position: relative; }
  @media screen and (max-width: 992px) {
    .filter-block {
      display: none; } }
  .filter-block__btn {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 1.25vmax;
    background-color: transparent;
    border: none;
