.filter-list {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  transform: translateX(-200%);
  flex-direction: column;
  align-items: flex-end;
  margin: 0;
  padding: 0;
  padding-bottom: 1.25vmax;
  padding-top: 1.25vmax;
  list-style: none;
  background-color: #242b2e;
  opacity: 0;
  transition: .5s; }
  .filter-list--active {
