  .filter-list__item {
    width: 100%;
    height: 100%;
    text-align: right; }
  .filter-list__item a {
    display: inline-block;
    width: 100%;
    padding: 0.41vmax 1.25vmax;
    font-size: 0.9vmax;
    line-height: 1;
    text-decoration: none;
    color: #616669;
    transition: .5s; }
    .filter-list__item a:hover, .filter-list__item a:focus {
      color: #fff; }
  .filter-list__item--active a {
    color: #fff; }
